.footer{
   border-top: 1px solid var(--color-stroke-quaternary,#DFDFDF);
   margin-top: 2px;
  margin-bottom: 35px;
  padding-bottom: 9px;
   
}

.footer-nyt-logo{
    width: 184px;
    height: 44px;
}

.footer-header{
    display: flex;
    justify-content: space-between;
}

.footer-to-home-div{
    font-size: 11px;
    font-weight: 400;
    cursor: pointer; 
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.footer-inner-div{
    margin-top: 10px;
    display: flex;
    border-bottom: 1px solid var(--color-stroke-quaternary,#DFDFDF);
    padding-bottom: 9px;
   
}

.footer-subcategory{
    list-style: none;
  
}

.footer-subcategories{
   margin: 0;
   padding: 0;
  
}

.footer-subcategory-link{
    text-decoration: none;
    color:black;
   font-size: 14px;
   font-weight: 400;
   line-height: 25px;

}

.footer-icons{
    width: 13px;
    height: 13px;
    margin-right:7px;
}

.footer-section-title{
    font-size: 13px;
    font-weight: 700;
    margin-top: 0;
   
}

.footer-section{
  
    width: 197px;
}

.footer-subcategory-link-right-div{
    font-size: 16px;
    font-weight: 700;
   
  
}

.footer-subcategory-right-div{
    height: 25px;
    list-style: none;
    display: flex;
    margin-bottom: 5px;
}

.footer-right-section{
    border-left:  1px solid var(--color-stroke-quaternary);
 
    padding-left: 15px;
}

.newsletter-div-ul{
    list-style: none;
    padding:0;
    font-size: 11px;
    font-weight: 400;
    line-height: 16px;
  
}

.newsletter-div-li{
    font-size: 11px;
    font-weight: 400;
    line-height: 20px;
}

.footer-upper-right-div{
    border-bottom: 1px solid var(--color-stroke-quaternary);
}

.copyright-div{
    display: flex;
    font-size: 11px;
    font-weight: 400;
    line-height: 20px;
    padding-top: 9px;
    justify-content: space-around;
    padding-left: 0px;
}

.footer-copy-right-li{
    padding: 10px 0px;
    list-style: none;
    color: #666
}