.latest-section-list-section{
    padding-top: 20px;
   
    
   
}
.last-list-item{
    border-bottom: 0;
    display: flex;
    margin-bottom: 20px;
    padding-bottom: 20px;
}

.last-list-item:hover h3{
    text-decoration: underline;
    }
  
.latest-section-list:hover h3{
text-decoration: underline;
}

.latest-section-left-div{
    max-width: 70%;
    
}

.latest-section-ordered-list{
    list-style: none;
}

.latest-section-ordered-list{
    list-style: none;
    padding-left: 0;
}

.latest-section-list{
    display: flex;
    margin-bottom: 20px;
    border-bottom: 1px solid var(--color-stroke-quaternary,#DFDFDF);
    padding-bottom: 20px;

}

.latest-list-section-figure{
    margin: 0;
    margin-left: 40px;
    display: inline-block;
    margin-left: 40px;
}

.latest-list-section-img{
    width: 205px;
    height: 136px;
    object-fit: contain;
}



.article-latest-section-list{
    display: flex;
    padding-left: 60px;
    align-items: flex-start;
    justify-content: flex-start;
}

.article-latest-section-inner-div{
   min-width: 478px;
  
}
.formatted-date{
    white-space: nowrap;
    font-size: 11px;
    color:var(--color-content-quaternary,#727272)
}

.latest-list-section-abstract{
    font-size: 17px;
    margin-top: 5px;
    font-family: 'EB Garamond', serif;
}

.dispatch-title-h3{
    font-family: 'EB Garamond', serif;
    font-size: 23px;
   

}